<template>
  <div role="main">
    <div id="loginHeader" class="row text-title" role="heading" aria-level="1">
      Aanmeldingsopties
    </div>
    <div id="credentialList" class="form-group" role="listbox">
      <div class="tile-container">
        <div class="row tile">
          <div
            class="table"
            tabindex="0"
            role="option"
            aria-label="Aanmelden met GitHub"
          >
            <div class="table-cell tile-img">
              <div>
                <img
                  class="tile-img"
                  role="presentation"
                  src="https://logincdn.msauth.net/16.000.28253.1/images/CredentialOptions/cred_option_github.svg?x=a7c769e31a628e643841187d20e149e3"
                />
              </div>
            </div>
            <div class="table-cell text-left content" @click="githubSignIn">
              <div>Aanmelden met GitHub</div>
              <div><small>Alleen persoonlijke accounts</small></div>
            </div>
            <div class="table-cell tile-badge">
              <span
                class="help-button"
                role="button"
                tabindex="0"
                aria-label="Meer informatie over deze aanmeldoptie"
              >
                <img
                  role="presentation"
                  src="https://logincdn.msauth.net/16.000.28253.1/images/documentation.svg?x=bcb4d1dc4eae64f0b2b2538209d8435a"
                  @click="githubDialog"
                />
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="tile-container">
        <div class="row tile">
          <div
            class="table"
            tabindex="0"
            role="option"
            aria-label="Mijn gebruikersnaam vergeten"
          >
            <div class="table-cell tile-img">
              <div>
                <img
                  class="tile-img"
                  role="presentation"
                  src="https://logincdn.msauth.net/16.000.28253.1/images/CredentialOptions/cred_option_forgot.svg?x=2ca2ea8917f97aef47d00e9b01d11d60"
                />
              </div>
            </div>
            <div class="table-cell text-left content" @click="forgotUsername">
              <div>Mijn gebruikersnaam vergeten</div>
              <div><small>Alleen persoonlijke accounts</small></div>
            </div>
            <div class="table-cell tile-badge"></div>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div class="row">
        <div class="col-xs-24 no-padding-left-right">
          <div class="inline-block" style="float: right;">
            <input
              type="button"
              id="idBtn_Back"
              class="btn btn-block"
              value="Terug"
              @click="clickBack"
            />
          </div>
          <div class="inline-block" style="float: right;">
            <input
              type="submit"
              id="idSIButton9"
              class="btn btn-block btn-primary"
              value="Volgende"
              disabled=""
              style="display: none;"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChooseLoginView",
  methods: {
    openNewTab(url) {
      Object.assign(document.createElement("a"), {
        target: "_blank",
        href: url
      }).click();
    },
    clickBack() {
      this.$emit("back");
    },
    githubDialog() {
      this.$emit("show-dialog");
    },
    githubSignIn() {
      this.openNewTab("//github.com/login");
    },
    forgotUsername() {
      this.openNewTab("//account.live.com/username/recover");
    }
  }
};
</script>
